import {  
    mapMutations,
	mapState
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	 computed: {
		...mapState(['hasLogin','userInfo'])
	},
	methods:{
		...mapMutations(['logout']),

		navTo(url){
			/* this.$api.msg(`跳转到${url}`); */
			if(!this.hasLogin){
				url = '/pages/login/login';
			}
			uni.navigateTo({  
				url
			})  
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
			    		setTimeout(()=>{
			    			uni.navigateBack();
			    		}, 200)
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
